.reviews{
  padding-top: 80px; 
  padding-bottom: 86px;
}

.reviews-quote-container{
  position: relative;
}

.reviews-photo {
  display: block;
  margin: 0 auto 72px;
}

.reviews-text {
  position: relative;
  z-index: 2;
  text-align: center;
  color: #000;
  font-size: 12px;
  font-weight: 500;
  line-height: 2.6;
  padding: 0 20%;
}

.reviews-name {
  text-align: center;
  font-size: 14px;
  font-weight: 500;
}

.reviews-svg-quote {
  position: absolute;
  top: -5%;
  left: 20%;
  fill: #e1e1e1;
  width: 54px;
  height: 40px;
}

.prev,
.next {
  position: absolute;
  top: 40%;
  width: auto;
  padding: 30px 15px;
  margin: 0 -175px;
  color: #c7133e;
  font-size: 18px;
  transition: all 500ms ease-in-out;
  border-radius: 0 3px 3px 0;

  &:hover {
    background-color: #ff003c;
    color: #ffffff;
  }

  @media screen and (max-width: 768px) {
    display: none;
  }
}

.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}
.prev{
  left: 0;
}

.dot {
  height: 15px;
  width: 15px;
  background-color: #c7133e;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active,
.dot:hover {
  background-color: #ff003c;
}
.fade {
  animation-name: fade;
  animation-duration: 1500ms;
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
